<template>
<!-- eslint-disable -->
  <div>
    <b-modal
      size="xl"
      id="purchaseDetails"
      hide-footer
      scrollable
    >
     <div class="px-2 animated fadeIn">
       <h3>Pre-Aprobar Solicitud # {{purchase.id}}</h3>
       <hr>
       <h2 class="py-2">Descripción de la Compra</h2>
       <br>
         <p class="text-justify">
         {{ purchase.description }}
      </p>
     </div>
     <!-- <b-row>
      
     </b-row> -->
      <hr>
     <b-table
      ref="refPurchaseDetails"
      class="position-relative animated fadeIn"
      :items="purchase_details"
      :fields="columns"
      responsive
      primary-key="id"
      :sort-by.sync="sortBy"
      show-empty
      :empty-text="empty"
      :sort-desc.sync="isSortDirDesc"
      :filter="filter"
      :empty-filtered-text="empty"
      :current-page="currentPage"
      :per-page="perPage"
      >

      <!-- Column: Centro de Costo -->
      <template #cell(cost_center)="data">
          <span class="text-nowrap">{{data.item.cost_center.description}}</span >
      </template>

      <!-- Column: Prioridad -->
      <template #cell(priority)="data">
        <b-form-select
          v-model="data.item.priority"
          :options="priorities"
        ></b-form-select> 
      </template>

      <!-- Column: Costo -->
      <template #cell(cost)="data">
        <b-form-input
          v-model="data.item.cost"
        />
      </template>

      <!-- Column: Cantidad Preaprobada-->
      <template #cell(preapproved_quantity)="data">
        <b-form-input
          v-model="data.item.preapproved_quantity" 
        />
      </template>

        <template #prev-text>
          <feather-icon
            icon="ChevronLeftIcon"
            size="18"
          />
        </template>
        <template #next-text>
          <feather-icon
            icon="ChevronRightIcon"
            size="18"
          />
        </template>
      </b-table>
    </b-modal>

  </div>
</template>

<script>

/*eslint-disable*/

import { 
  BModal, BButton, VBModal, BAlert,
} from 'bootstrap-vue'

import Ripple from 'vue-ripple-directive'

// Servicios
import * as costCenterService from '@/services/cost_center'

export default {
  components: {
    BButton,
    BModal,
    BAlert,
  },
  directives: {
    'b-modal': VBModal,
    Ripple,
  },
  props: {

    purchase : {
      type: Object,
      default: () => ({}),
    },

    purchase_details : {
      type: Array,
      default: () => [],
    },
  },

  computed: {
    costo: {
      get () { return this.purchaseDetails.cost },
      set (value) { this.purchaseDetails.cost = value  },
    },
  },
  data() {
    return {
      empty: 'No hay datos para mostrar',
      pageLength: 5,
      dir: false,
      priority: null,
      priorities: [
      //  { value: null, text: 'Seleccione' },
       { text: 'Baja', value: 1 },
       { text: 'Alta', value: 2 },
      ],
      columns: [
        { key: 'article_reference', label: 'Referencia', sortable: true },
        { key: 'cost_center', label: 'Centro De Costo', sortable: true },
        { key: 'priority', label: 'Prioridad', sortable: true },
        { key: 'cost', label: 'Costo', sortable: true },
        { key: 'quantity_requested', label: 'Cantidad Solicitada', sortable: true },
        { key: 'preapproved_quantity', label: 'Cantidad Preaprobada', sortable: true },
        // { key: 'unit_price', label: 'Precio Unitario', sortable: true },
        // { key: 'total_price', label: 'Precio Total', sortable: true },
        // { key: 'status', label: 'Estado', sortable: true },
        // { key: 'actions', label: 'Acciones', sortable: false },
        // {
        //   label: 'Estado',
        //   field: 'status',
        // },
        // {
        //   label: 'Opciones',
        //   field: 'action',
        // },
      ],
      rows: [],
      perPage: 10,
      sortBy: 'id',
      isSortDirDesc: true,
      perPageOptions: [5, 10, 25, 50],
      currentPage: 1,
      filter: '',
    }
  },

  // created: {
  //   beforeEnter(to, from, next) {
  //     costCenterService.getAll()
  //       .then(response => {
  //         this.rows = response.data
  //         next()
  //       })
  //       .catch(error => {
  //         console.log(error)
  //       })
  //   },
  // },



  created() {
    this.getCostCenters()   
  },

  mounted() {
    this.purchaseDetails
  },

  methods: {

    onInput(event) {
      this.$emit('update', event.target.value)
      console.log(event)
    },

    changeCost(cost) {
      // this.cost = cost
      // this.purchaseDetails.forEach(item => {
      //   if (item.id === id) {
      //     item.cost = cost
      //   }
      // })
      console.log(cost)
    },

    async getCostCenters() {
      try {
        const response = await costCenterService.getCostCenters()

        if (response.status === 200) {
          // this.loading = false
          this.rows = response.data.data
        }
      } catch (error) {
        // this.loading = false
      }
    },
  }
}

</script>