<template>
<!-- eslint-disable -->

  <div>

    <!-- Filtro Solicitudes -->
    <!-- <users-list-filters
      :role-filter.sync="roleFilter"
      :plan-filter.sync="planFilter"
      :status-filter.sync="statusFilter"
      :role-options="roleOptions"
      :plan-options="planOptions"
      :status-options="statusOptions"
    /> -->

    <!-- Table Container Card -->
    <b-card
      no-body
      class="mb-0"
    >

      <div class="m-2">

        <!-- Table Top -->
        <b-row>

          <!-- Per Page -->
          <b-col
            cols="12"
            md="6"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Ver</label>
            <v-select
              v-model="perPage"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="perPageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>Entradas</label>
          </b-col>

          <!-- Search -->
          <b-col
            cols="12"
            md="6"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="filter"
                class="d-inline-block mr-1"
                placeholder="Buscar..."
              />
              <b-button
                variant="primary"
                @click="addPurchase"
              >
                <span class="text-nowrap">Agregar</span>
              </b-button>
            </div>
          </b-col>
        </b-row>

      </div>

      <b-table
        v-if="!loading"
        ref="refPurchaseTable"
        class="position-relative animated fadeIn"
        :items="items"
        :fields="columns"
        responsive
        primary-key="id"
        :sort-by.sync="sortBy"
        show-empty
        :empty-text="empty"
        :sort-desc.sync="isSortDirDesc"
        :filter="filter"
        :empty-filtered-text="empty"
        :current-page="currentPage"
        :per-page="perPage"
      >

      <!-- <template slot="top-row" slot-scope="{ fields }">
        <td v-for="field in fields" :key="field.key">
          <input v-model="filters[field.key]" :placeholder="field.label">
        </td>
      </template> -->

        <!-- Column: Origen Solicitud -->
        <template #cell(origin_purchase)="data">        
            <span class="text-nowrap">
              {{ data.item.origin_purchase.description }}
            </span>          
        </template>

        <!-- Column: Mantenimiento - Prioridad -->
        <template #cell(priority)="data">        
            <span class="text-nowrap">
              {{ data.item.priority.description }}
            </span>          
        </template>

        <!-- Column: Origen Solicitud -->
        <template #cell(origin_purchase)="data">        
            <span class="text-nowrap">
              {{ data.item.origin_purchase.description }}
            </span>          
        </template>

        <!-- Column: Tipo de Solicitud -->
        <template #cell(purchase_type)="data">        
            <span class="text-nowrap">
              {{ data.item.purchase_type.description }}
            </span>          
        </template>

        <!-- Column: Notificaciones -->
        <template #cell(notifications)="data">
          <b-button
            v-b-modal.notificationPurchase
            variant="primary"
            @click="showNotifications(data.item)"         
          >{{ data.item.notifications.length }}
          </b-button>
          <!-- <b-form-checkbox
            :checked="[data.item.status == 1 ? 'true' : 'false']"
            :class="[data.item.status == 1 ? 'custom-control-success' : 'custom-control-secondary']"
            name="check-button"
            switch
            @change="changeStatus(data.item.id)"
          /> -->
        </template>

        <!-- Column: Actions -->
        <template #cell(actions)="data">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >

            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="16"
                class="align-middle text-body"
              />
            </template>
            <!-- eslint-disable-next-line -->
            <b-dropdown-item 
              v-b-modal.purchaseDetails
              @click="editPurchase(data.item, data.item.purchase_details)"
            >
              <feather-icon icon="EditIcon" />
              <span class="align-middle ml-50">Editar</span>
            </b-dropdown-item>
          </b-dropdown>
        </template>

      </b-table>
      <div class="mx-2 mb-2">
        <b-row>

          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">Showing {{ dataMeta.from }} to {{ dataMeta.to }} of {{ dataMeta.of }} entries</span>
          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >

            <b-pagination
              v-model="currentPage"
              :total-rows="rows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>

          </b-col>

        </b-row>
      </div>

      <loading :loading="loading" />

    </b-card>

    <PurchaseDetails
      ref="purchaseDetails"
      :purchase_details="this.purchaseDetails"
      :purchase="this.purchase"
    />

    <NotificationPurchase
      ref="notificationPurchase"
      :notifications="this.notifications"
      :idPurchase="this.idPurchase"
      @reload="getPurchases"
    />

  </div>
</template>

<script>

/*eslint-disable*/

// Bootstrap
import {
  BCard, BRow, BCol, BFormInput, BButton, BTable, BMedia, BAvatar, BLink,
  BBadge, BDropdown, BDropdownItem, BPagination,
} from 'bootstrap-vue'

// vSelect
import vSelect from 'vue-select'

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

// Moment - Fechas
import moment from 'moment'
import 'moment/locale/es'

// Servicios
import * as purchaseService from '@/services/purchases/purchases'

// Componentes
import PurchaseDetails from './PurchaseDetails.vue'
import NotificationPurchase from './NotificationPurchase.vue'
import UsersListFilters from './UsersListFilters.vue'

export default {

  name : "PurchasesAdmin",

  components: {
    ToastificationContent,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BButton,
    BTable,
    BMedia,
    BAvatar,
    BLink,
    BBadge,
    BDropdown,
    BDropdownItem,
    BPagination,
    vSelect,
    PurchaseDetails,
    NotificationPurchase,
    UsersListFilters,
},
  data() {
    return {
      loading: true,
      items: [],
      purchase: {},
      purchaseDetails: [],
      notifications: [],
      idPurchase: null,
      empty: 'No hay datos para mostrar',
      columns: [
        { key: 'notifications', label: 'Novedades', sortable: true },
        { key: 'id', label: 'Comprobante', sortable: true },
        { key: 'origin_purchase', label: 'Origen Solicitud', sortable: true },
        { key: 'maintenance_id', label: 'Comprobante Origen', sortable: true },
        { key: 'priority', label: 'Mantenimiento', sortable: true },
        { key: 'purchase_type', label: 'Tipo Solicitud', sortable: true },
        { key: 'user.name', label: 'Solicitante', sortable: true },        
        { key: 'total_quantity', label: 'Cantidad total solicitada', sortable: true },
        { key: 'quantity_products', label: 'Cantidad de Productos', sortable: true },
        { key: 'created_at', label: 'Fecha solicitud', sortable: true, formatter: 'moment' },
        { key: 'actions', label: 'Opciones', sortable: true },
      ],
      perPage: 10,
      sortBy: 'id',
      isSortDirDesc: true,
      perPageOptions: [5, 10, 25, 50],
      currentPage: 1,
      filter: '',
      // filters: {
      //   id: '',
      //   origin_purchase : '',
      //   purchase_type : '',
      // },
    }
  },

  computed: {
    // filtered () {
    //   const filtered = this.items.filter(item => {
    //     return Object.keys(this.filters).every(key =>
    //         String(item[key]).includes(this.filters[key]))
    //   })
    //   return filtered.length > 0 ? filtered : [{
    //     id: '',
    //     origin_purchase : '',
    //     purchase_type : '',
    //   }]
    // },
    rows() {
      return this.items.length
    },
    dataMeta() {
      return {
        from: (this.currentPage - 1) * this.perPage + 1,
        to: this.currentPage * this.perPage > this.rows ? this.rows : this.currentPage * this.perPage,
        of: this.rows,
      }
    },
  },

  // computed: {
  //   dataMeta() {
  //     const localItemsCount = refInventoryListTable.value ? refInventoryListTable.value.items.length : 0
  //     return {
  //       // from: (this.currentPage - 1) * this.perPage + 1,
  //       // to: Math.min(this.currentPage * this.perPage, this.rows),
  //       // of: this.rows,
  //       from: perPage.value * (currentPage.value - 1) + (localItemsCount ? 1 : 0),
  //       to: perPage.value * (currentPage.value - 1) + localItemsCount,
  //       of: totalUsers.value,
  //     }
  //   },
  // },

  watch: {
    currentPage: { handler: 'getInventories', deep: true },
  },


  created() {
    this.getPurchases()
  },

  mounted() {
    // Consulta los datos cada 5 minutos
    setInterval(() => {
      this.getPurchases()
    }, 300000)
  },

  methods: {

    moment(date) {
      return moment(date).format('LLLL');
    },

    async getPurchases() {
      try {
        const response = await purchaseService.getPurchases()
        if (response.status === 200) {
          this.loading = false
          this.items = response.data.data
        }
      } catch (error) {
        this.loading = false
        // this.showToast('Warning', 'AlertCircleIcon', 'No se pudieron obtener los datos', 'warning')
      }
    },

    addPurchase() {
      this.$router.push({ name: 'purchases-add' })
    },

    editPurchase(purchase, data) {
      this.purchase = purchase
      this.purchaseDetails = data
    },

    showNotifications(purchase) {
      this.notifications = purchase.notifications
      console.log(this.notifications);
      this.idPurchase = purchase.id
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },

}
</script>

<style lang="scss" scoped>
  .per-page-selector {
    width: 90px;
  }
  @import '@core/scss/vue/libs/vue-select.scss';
</style>
