<template>
<!-- eslint-disable -->
  <div>
    <b-modal
      size="xl"
      id="notificationPurchase"
      ref="notificationModal"
      hide-footer
      scrollable
      no-stacking 
    >
     <div class="px-2 animated fadeIn">
       <h2>Novedades de la Compra #{{idPurchase}}</h2>
     </div>

     <hr>
      
     <b-table
      ref="notificationPurchase"
      class="position-relative animated fadeIn"
      :items="notifications"
      :fields="columns"
      responsive
      primary-key="id"
      :sort-by.sync="sortBy"
      show-empty
      :empty-text="empty"
      :sort-desc.sync="isSortDirDesc"
      :filter="filter"
      :empty-filtered-text="empty"
      :current-page="currentPage"
      :per-page="perPage"
      >

      <!-- Column: User -->
      <template #cell(user)="data">
        <span class="text-nowrap">
          {{data.item.user.name}}
        </span>
      </template>

        <template #prev-text>
          <feather-icon
            icon="ChevronLeftIcon"
            size="18"
          />
        </template>
        <template #next-text>
          <feather-icon
            icon="ChevronRightIcon"
            size="18"
          />
        </template>
      </b-table>
        <div class="text-right my-2">
          <button
            type="button"
            class="btn btn-danger btn-sm mx-1"
            @click="hideNotificationModal"
          >
            Cancelar
          </button>
          <button
            type="button"
            class="btn btn-primary btn-sm"
            v-b-modal.modalNotification
          >
            <span class="fa fa-check" /> Registrar Novedad
          </button>
        </div>
    </b-modal>

    <!-- Modal Para Agregar Notificaciones -->
    <b-modal
      id="modalNotification"
      ref="modalNotification"
      title="Notificar"
      hide-footer
    >
      <div class="px-2 animated fadeIn">
        <h3 class="mb-1">Registrar Novedad de la compra #{{idPurchase}}</h3>
        <hr>
         <validation-observer 
            ref="form_notification" 
            #default="{invalid}">

          <b-form @submit.prevent>           
              <b-row>
                <!-- Mensaje -->                
                <b-col cols="12">                  
                    <b-form-group label="Mensaje" label-for="v-message">
                    <validation-provider #default="{ errors }" name="Mensaje" rules="required">
                        <b-form-textarea id="v-message" v-model="notification.message"
                            placeholder="Mensaje de la Notificación" :state="errors.length > 0 ? false:null" />
                        <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                    </b-form-group>
                </b-col>        
                                
                <!-- Usuarios -->
                <b-col cols="12">
                    <b-form-group label="Usuario" label-for="usuarios">
                        <validation-provider #default="{ errors }" name="Usuario" rules="required">
                            <!-- <v-select v-model="notification.user_id" label="name"
                                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'" :options="users"
                                :reduce="users => users.id" :clearable="false" input-id="usuarios"
                                placeholder="Seleccione el usuario" /> -->
                                <select name="" id="" v-model="notification.receiver_id">
                                    <option value="" v-for="user in users" :key="user.id" :value="user.id">{{ user.name }}</option>
                                </select>
                            <b-form-invalid-feedback :state="errors.length > 0 ? false:null">
                                {{ errors[0] }}
                            </b-form-invalid-feedback>
                        </validation-provider>
                    </b-form-group>
                </b-col>

                  <!-- Botones de Agregar y Cancelar -->
                  <b-col cols="12" class="my-1 text-right">                   
                      <b-button size="sm" v-ripple.400="'rgba(186, 191, 199, 0.15)'" type="reset" variant="outline-danger" class="mr-1" @click="hideNotificationAddModal">
                          Cancelar
                      </b-button>
                      <b-button size="sm" v-ripple.400="'rgba(255, 255, 255, 0.15)'" type="submit" variant="primary" :disabled="invalid" @click="addNotification">
                          Registrar
                      </b-button>
                  </b-col>
              </b-row>
          </b-form>
        </validation-observer>
      </div>
    </b-modal>

  </div>
</template>

<script>

/*eslint-disable*/

import { 
  BModal, BButton, VBModal, BAlert,
} from 'bootstrap-vue'

import Ripple from 'vue-ripple-directive'

import vSelect from 'vue-select'

import { ValidationProvider, ValidationObserver } from 'vee-validate'
import { required, alphaNum } from '@validations'

// Moment - Fechas
import moment from 'moment'
import 'moment/locale/es'

// Servicios
import * as userService from '@/services/user'
import * as notificationService from '@/services/notifications/notifications'

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  components: {
    BButton,
    BModal,
    BAlert,
    vSelect,
    ToastificationContent,
    // Form Validation
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    'b-modal': VBModal,
    Ripple,
  },
  props: {

    notifications : {
      type: Array,
      default: () => [],
    },

    idPurchase : {
      type: Number,
      default: () => 0,
    },

  },

  data() {
    return {
      empty: 'No hay datos para mostrar',
      pageLength: 5,
      dir: false,
      required,
      alphaNum,
      users: [],
      notification : {
        message: '',
        purchase_id: '',
        receiver_id: null,        
      },
      columns: [
        { key: 'message', label: 'Descripción', sortable: true },
        { key: 'user', label: 'Usuario que envia', sortable: true },
        { key: 'created_at', label: 'Fecha', sortable: true, formatter: (value) => moment(value).format('LLL') },
      ],
      perPage: 200,
      sortBy: 'id',
      isSortDirDesc: true,
      perPageOptions: [5, 10, 25, 50],
      currentPage: 1,
      filter: '',
    }
  },

  created() {
    this.getUsers()
  },

  methods: {

    hideNotificationModal() {
      this.$refs.notificationModal.hide()
    },

    hideNotificationAddModal() {
      this.$refs.modalNotification.hide()
    },

    async getUsers() {
      try {
        const response = await userService.getUsers()
        if (response.status === 200) {
          this.users = response.data.data
        }
      } catch (error) {
        console.log(error)
      }
    },

    async addNotification() {
      try {
        this.notification.purchase_id = this.idPurchase
        const response = await notificationService.addNotification(this.notification)
        if (response.status === 200) {
          this.hideNotificationAddModal()
          this.$emit("reload")
          this.showToast('Agregado', 'CheckIcon', 'Notificación Agregada', 'success')
        }     
      } catch (error) {
          this.showToast('Advertencia!', 'AlertCircleIcon', 'Ocurrió un error al agregar la notificación', 'warning')
      }
    },

    hideNotificationAddModal() {
      this.$refs.form_notification.reset()
      this.$refs.modalNotification.hide()
      this.cleanForm()
    },

    cleanForm() {
      this.notification = {
        message: '',
        purchase_id: '',
        receiver_id: null,        
      }
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },
   
  }
}

</script>