import { http } from './http_service'

export function getCostCenters() {
  return http().get('/cost_centers')
}

export function getCostCentersActive(active) {
  return http().get(`/cost_centers/${active}`)
}

export function addCostCenter(data) {
  return http().post('/cost_center', data)
}

export function activateDesactivate(id) {
  return http().post(`/cost_center/${id}`)
}

export function getCostCenterById(id) {
  return http().get(`/cost_center/${id}`)
}

export function updateCostCenter(id, data) {
  return http().put(`/cost_center/${id}`, data)
}
